import * as React from 'react'
import { Link } from "gatsby"
import {
    linkItem,
    navBar,
  } from './tabBar.module.css'

const TabBar = () => {
    return (
      <nav className={navBar}>
        <ul>
            <Link to='/' className={linkItem}>About Me</Link>
            <Link to="/experience/" className={linkItem}>Experience</Link>
            <Link to='/portfolio/' className={linkItem}>Portfolio</Link>
        </ul>
      </nav>
    )
  }


  export default TabBar