import * as React from 'react'
import Layout from '../components/layout'


import * as containerStyles from '../components/layout.module.css'


// Define a constant for the maximum level
const MAX_LEVEL = 5;

const SkillBar = ({ name, level }) => {
  // Create an empty array to store the icons
  const icons = [];

  // Loop from 1 to the maximum level
  for (let i = 1; i <= MAX_LEVEL; i++) {
    // If the current index is less than or equal to the level, use a solid circle icon
    if (i <= level) {
      icons.push(
        <div className={containerStyles.circle} style={{ "--level": i }} />
      );
    }
    // Otherwise, use a regular circle icon
    else {
      icons.push(
        <div className={containerStyles.circleEmpty} style={{ "--level": i }} />
      );
    }
  }

  // Return a JSX element with the name and the icons
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
        maxWidth: "20rem",
      }}
    >
      <span style={{ marginRight: "0.5rem" }}>{name}</span>
      <div style={{ display: "flex", alignItems: "center" }}>{icons}</div>
    </div>
  );
};

const IndexPage = () => {
  // Define an array of skills with names and levels
  const skills = [
    { name: "Adobe Premiere Pro", level: 5 },
    { name: "Adobe After Effects", level: 3 },
    { name: "Adobe Photoshop", level: 5 },
    { name: "Adobe InDesign", level: 2 },
    { name: "Resolume Arena", level: 3 },
    { name: "HTML & CSS", level: 3 },
    { name: "ReactJS", level: 2 },
  ];

  return (
    <Layout pageTitle="About Me">

      <>
      <header>
        <h1>Pawel Glowacki</h1>
        <h2>Video editor / Graphic designer</h2>
      </header>
      </>

      <>
      <section className={containerStyles.aboutMe}>
        <h2>ABOUT ME</h2>
        <p>I'm a  video editor and graphic designer with 6+ years of experience, currently living in Oslo, Norway. Successfully utilized multiple skills and vast technical knowledge to create social media content, video, print, and web design. In recent years my main focus was editing and post-production of video for numerous clients. I love to tackle challenges that each project provides and think up of a creative angle to aproach it.</p>
      </section>
      <section className={containerStyles["aboutMe"]}>
        <h3>SKILLS</h3>
        <ul>
        {skills.map((skill) => (
          // Use the SkillBar function for each skill
          <li key={skill.name}>
            <SkillBar name={skill.name} level={skill.level} />
          </li>
        ))}
          
        </ul>
      </section>
      </>

    </Layout>
  )
}

export const Head = () => <title>Home Page - About Me</title>

export default IndexPage
